<template>
  <div class="app-page voucher-center">
    <page-content>
      <div class="top-title vertical-center">
        <div><span>账户金额&emsp;2000.00&nbsp;元</span></div>
        <div class="top-title-right vertical-center">
          <span>充值记录</span>
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="denominations">
        <div class="den-list">
          <div class="den-item" :class="{'den-item-active': item.isSelect}" v-for="(item, index) in denominations.list" :key="index" @click="clickItem(denominations, item, index)">
            <div class="price-original">{{item.priceOriginal}}元</div>
            <div class="price-sell">售价:{{item.priceSell}}元</div>
          </div>
        </div>
      </div>
      <div class="submit-div flex-center">
        <van-button :disabled="!denominations.selectOne.isSelect">立即支付</van-button>
      </div>
    </page-content>
  </div>
</template>

<script>
import PageContent from '@/components/page/content'
export default {
  name: 'vouchercenter',
  components: {PageContent},
  data () {
    return {
      // 面额
      denominations: {
        selectOne: { id: '-1', priceOriginal: '', priceSell: '', isSelect: false },
        list: [{ id: '1', priceOriginal: '100', priceSell: '80', isSelect: false }, { id: '2', priceOriginal: '100', priceSell: '80', isSelect: false },
          { id: '3', priceOriginal: '100', priceSell: '80', isSelect: false }, { id: '4', priceOriginal: '100', priceSell: '80', isSelect: false },
          { id: '5', priceOriginal: '100', priceSell: '80', isSelect: false }, { id: '6', priceOriginal: '100', priceSell: '80', isSelect: false }]
      }
    }
  },
  created () {
  },
  mounted () {
  },
  computed: {
  },
  methods: {
    // 点击某个选项触发
    clickItem (data, curItem, index) {
      if (!curItem.isSelect) {
        data.list = data.list.map(item => {
          item.isSelect = false
          return item
        })
        curItem.isSelect = true
      }
      data.selectOne = curItem
    }
  },
  watch: {}
}

</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import '../../../styles/variables';
.voucher-center{
  font-family: $font-family-default;
  .page-content{
    width: 100%;
    position: relative;
    .top-title{
      width: 100%;
      height: 90px;
      background-color: $green22ac38;
      padding: 0 26px;
      opacity: .8;
      font-size: 30px;
      display: flex;align-items: center;justify-content: space-between;
      color: $whiteffffff!important;
      .top-title-right{
        height: 80px;
      }
    }
    .denominations{
      width: 100%;height: calc(100% - 230px);
      overflow: auto;
      padding: 30px 30px;
      .den-list{
        width: 100%;
        display: flex;justify-content: space-between;flex-flow:row wrap;
        .den-item{
          width: calc(33.33% - 13.3px);height: 128px;
          /*border: 1.2px solid $black999999;*/
          border-radius: 10px;
          margin-bottom: 20px;
          color: $black999999;
          box-shadow: darkgray -2px 0px 2px,
                      darkgray 0px -2px 2px,
                      darkgray 2px 0px 2px,
                      darkgray 0px 2px 2px;
          display: flex;flex-direction: column;justify-content: center;align-items: center;
          &-active{
            border: 1.2px solid $green22ac38;
            color: $green22ac38;
            box-shadow: $green22ac38 -2px 0px 2px,
            $green22ac38 0px -2px 2px,
            $green22ac38 2px 0px 2px,
            $green22ac38 0px 2px 2px;
          }
          .price-original{
            font-size: 36px;
          }
          .price-sell{
            font-size: 24px;
            margin-top: 10px;
          }
        }
      }
    }
    .submit-div{
      width: 100%;height: 76px;
      position: absolute;bottom: 60px;
      /deep/ .van-button{
        width: 90%;height: 100%;
        border-radius: 10px;
        background: -webkit-linear-gradient(left top, $green22ac38, $green1a7829); /* Safari 5.1 - 6.0 */
        background: linear-gradient(to bottom right, $green22ac38, $green1a7829);
        color: $whiteffffff;
        line-height: 0;font-weight: 600;
        font-family: $font-family-Bold;
      }
    }
  }
}
</style>



